<template>
  <el-container class="query-el-container">
    <el-header class="query-el-header">
      <el-page-header @back="goBack" content="成绩查询"/>
    </el-header>
    <el-main class="query-el-main">
      <el-table :data="tableData" class="query-el-table" height="638px">
        <el-table-column
          prop="order"
          label="编号"
          width="120px">
        </el-table-column>
        <el-table-column
          prop="username"
          label="姓名"
          width="120px">
        </el-table-column>
        <el-table-column
          prop="direction"
          label="考题类别">
        </el-table-column>
        <el-table-column
          prop="category"
          label="所属套题">
        </el-table-column>
        <el-table-column
          prop="score"
          label="分数"
          width="120px">
        </el-table-column>
        <el-table-column
          prop="startDate"
          label="考试开始时间">
        </el-table-column>
        <el-table-column
          prop="endDate"
          label="考试结束时间">
        </el-table-column>
      </el-table>
      
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'MainScoreQuery',
  data() {
      return {
        tableData: []
      }
    },
  props: {
    ccurrentPersonMsg: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    getMsg() {
      let data = {}
      let scoreInfo = this.ccurrentPersonMsg.scoreInfo
      for(let i of scoreInfo) {
        data.order = i.order
        data.username = this.ccurrentPersonMsg.username
        data.direction = this.ccurrentPersonMsg.direction
        data.category = i.category
        data.score = i.score
        data.startDate = i.startDate
        data.endDate = i.endDate
        this.tableData.push(data)
        data = {}
      }
    }
  },
  created() {
    this.getMsg()
  }
}
</script>

<style>
  .query-el-container {
    margin: 0px;
  }
  .query-el-header {
    line-height: 60px;
  }
  .query-el-main {
    height: 72vh;
    margin: 0 32px;
  }
  .query-el-table .cell {
    text-align: center
  }
</style>